/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-7b246f90] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.native-datetime-picker[data-v-7b246f90] {
  display: flex;
  flex-direction: column;
}
.native-datetime-picker .native-datetime-picker--input[data-v-7b246f90] {
  width: 100%;
  flex: 0 0 auto;
  padding-right: 4px;
}
[data-theme-light] .native-datetime-picker--input[data-v-7b246f90],
[data-themes*=light] .native-datetime-picker--input[data-v-7b246f90] {
  color-scheme: light;
}
[data-theme-dark] .native-datetime-picker--input[data-v-7b246f90],
[data-themes*=dark] .native-datetime-picker--input[data-v-7b246f90] {
  color-scheme: dark;
}
@media (prefers-color-scheme: light) {
[data-theme-default] .native-datetime-picker--input[data-v-7b246f90],
  [data-themes*=default] .native-datetime-picker--input[data-v-7b246f90] {
    color-scheme: light;
}
}
@media (prefers-color-scheme: dark) {
[data-theme-default] .native-datetime-picker--input[data-v-7b246f90],
  [data-themes*=default] .native-datetime-picker--input[data-v-7b246f90] {
    color-scheme: dark;
}
}