/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-a688e724] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.item-list__entry[data-v-a688e724] {
  display: flex;
  align-items: flex-start;
  position: relative;
  padding: 8px;
}
.item-list__entry[data-v-a688e724]:hover, .item-list__entry[data-v-a688e724]:focus {
  background-color: var(--color-background-hover);
  border-radius: var(--border-radius-large);
}
.item-list__entry .item-avatar[data-v-a688e724] {
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
}
.item-list__entry .item__details[data-v-a688e724] {
  padding-left: 8px;
  max-height: 44px;
  flex-grow: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 44px;
}
.item-list__entry .item__details h3[data-v-a688e724],
.item-list__entry .item__details .message[data-v-a688e724] {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.item-list__entry .item__details .message span[data-v-a688e724] {
  width: 10px;
  display: inline-block;
  margin-bottom: -3px;
}
.item-list__entry .item__details h3[data-v-a688e724] {
  font-size: 100%;
  margin: 0;
}
.item-list__entry .item__details .message[data-v-a688e724] {
  width: 100%;
  color: var(--color-text-maxcontrast);
}
.item-list__entry .item-icon[data-v-a688e724] {
  position: relative;
  width: 14px;
  height: 14px;
  margin: 27px -3px 0px -7px;
}
.item-list__entry button.primary[data-v-a688e724] {
  padding: 21px;
  margin: 0;
}

