/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-f5b31440] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.app-navigation-caption[data-v-f5b31440] {
  display: flex;
  justify-content: space-between;
}
.app-navigation-caption--heading[data-v-f5b31440] {
  padding: var(--app-navigation-padding);
}
.app-navigation-caption--heading[data-v-f5b31440]:not(:first-child):not(:last-child) {
  padding: 0 var(--app-navigation-padding);
}
.app-navigation-caption__name[data-v-f5b31440] {
  font-weight: bold;
  color: var(--color-main-text);
  font-size: var(--default-font-size);
  line-height: 44px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  box-shadow: none !important;
  flex-shrink: 0;
  padding: 0 calc(var(--default-grid-baseline, 4px) * 2) 0 calc(var(--default-grid-baseline, 4px) * 3);
  margin-top: 0px;
  margin-bottom: 12px;
}
.app-navigation-caption__actions[data-v-f5b31440] {
  flex: 0 0 44px;
}
.app-navigation-caption[data-v-f5b31440]:not(:first-child) {
  margin-top: 22px;
}