/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-30270f02] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.option[data-v-30270f02] {
  display: flex;
  align-items: center;
  width: 100%;
  height: var(--height);
  cursor: inherit;
}
.option__avatar[data-v-30270f02] {
  margin-right: var(--margin);
}
.option__details[data-v-30270f02] {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: center;
  min-width: 0;
}
.option__lineone[data-v-30270f02] {
  color: var(--color-main-text);
}
.option__linetwo[data-v-30270f02] {
  color: var(--color-text-maxcontrast);
}
.option__lineone[data-v-30270f02], .option__linetwo[data-v-30270f02] {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 1.1em;
}
.option__lineone strong[data-v-30270f02], .option__linetwo strong[data-v-30270f02] {
  font-weight: bold;
}
.option__icon[data-v-30270f02] {
  width: 44px;
  height: 44px;
  color: var(--color-text-maxcontrast);
}
.option__icon.icon[data-v-30270f02] {
  flex: 0 0 44px;
  opacity: 0.7;
  background-position: center;
  background-size: 16px;
}
.option__details[data-v-30270f02], .option__lineone[data-v-30270f02], .option__linetwo[data-v-30270f02], .option__icon[data-v-30270f02] {
  cursor: inherit;
}