/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-959b70c1] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.breadcrumb[data-v-959b70c1] {
  width: 100%;
  flex-grow: 1;
  display: inline-flex;
  align-items: center;
}
.breadcrumb--collapsed[data-v-959b70c1] .vue-crumb:last-child {
  min-width: 100px;
}
.breadcrumb nav[data-v-959b70c1] {
  flex-shrink: 1;
  min-width: 0;
}
.breadcrumb .breadcrumb__crumbs[data-v-959b70c1] {
  max-width: 100%;
}
.breadcrumb .breadcrumb__crumbs[data-v-959b70c1], .breadcrumb .breadcrumb__actions[data-v-959b70c1] {
  display: inline-flex;
}