/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-7e250fb8] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.app-details-toggle[data-v-7e250fb8] {
  position: sticky;
  width: 44px;
  height: 44px;
  padding: 14px;
  cursor: pointer;
  opacity: 0.6;
  transform: rotate(180deg);
  background-color: var(--color-main-background);
  z-index: 2000;
  top: var(--app-navigation-padding);
  left: calc(var(--default-clickable-area) + var(--app-navigation-padding) * 2);
}
.app-details-toggle--mobile[data-v-7e250fb8] {
  left: var(--app-navigation-padding);
}
.app-details-toggle[data-v-7e250fb8]:active, .app-details-toggle[data-v-7e250fb8]:hover, .app-details-toggle[data-v-7e250fb8]:focus {
  opacity: 1;
}
