/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-219a1ffb] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.textarea[data-v-219a1ffb] {
  position: relative;
  width: 100%;
  border-radius: var(--border-radius-large);
  margin-block-start: 6px;
  resize: vertical;
}
.textarea__main-wrapper[data-v-219a1ffb] {
  position: relative;
}
.textarea--disabled[data-v-219a1ffb] {
  opacity: 0.7;
  filter: saturate(0.7);
}
.textarea__input[data-v-219a1ffb] {
  margin: 0;
  padding-inline: 10px 6px;
  width: 100%;
  font-size: var(--default-font-size);
  text-overflow: ellipsis;
  background-color: var(--color-main-background);
  color: var(--color-main-text);
  border: 2px solid var(--color-border-maxcontrast);
  border-radius: var(--border-radius-large);
  cursor: pointer;
}
.textarea__input[data-v-219a1ffb]:active:not([disabled]), .textarea__input[data-v-219a1ffb]:hover:not([disabled]), .textarea__input[data-v-219a1ffb]:focus:not([disabled]) {
  border-color: 2px solid var(--color-main-text) !important;
  box-shadow: 0 0 0 2px var(--color-main-background) !important;
}
.textarea__input[data-v-219a1ffb]:not(:focus, .textarea__input--label-outside)::placeholder {
  opacity: 0;
}
.textarea__input[data-v-219a1ffb]:focus {
  cursor: text;
}
.textarea__input[data-v-219a1ffb]:disabled {
  cursor: default;
}
.textarea__input[data-v-219a1ffb]:focus-visible {
  box-shadow: unset !important;
}
.textarea__input--success[data-v-219a1ffb] {
  border-color: var(--color-success) !important;
}
.textarea__input--success[data-v-219a1ffb]:focus-visible {
  box-shadow: rgb(248, 250, 252) 0px 0px 0px 2px, var(--color-primary-element) 0px 0px 0px 4px, rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.textarea__input--error[data-v-219a1ffb] {
  border-color: var(--color-error) !important;
}
.textarea__input--error[data-v-219a1ffb]:focus-visible {
  box-shadow: rgb(248, 250, 252) 0px 0px 0px 2px, var(--color-primary-element) 0px 0px 0px 4px, rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.textarea__label[data-v-219a1ffb] {
  position: absolute;
  margin-inline: 12px 0;
  max-width: fit-content;
  inset-block-start: 11px;
  inset-inline: 0;
  color: var(--color-text-maxcontrast);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  pointer-events: none;
  transition: height var(--animation-quick), inset-block-start var(--animation-quick), font-size var(--animation-quick), color var(--animation-quick), background-color var(--animation-quick) var(--animation-slow);
}
.textarea__input:focus + .textarea__label[data-v-219a1ffb], .textarea__input:not(:placeholder-shown) + .textarea__label[data-v-219a1ffb] {
  inset-block-start: -10px;
  line-height: 1.5;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-main-text);
  background-color: var(--color-main-background);
  padding-inline: 4px;
  margin-inline-start: 8px;
  transition: height var(--animation-quick), inset-block-start var(--animation-quick), font-size var(--animation-quick), color var(--animation-quick);
}
.textarea__helper-text-message[data-v-219a1ffb] {
  padding-block: 4px;
  display: flex;
  align-items: center;
}
.textarea__helper-text-message__icon[data-v-219a1ffb] {
  margin-inline-end: 8px;
}
.textarea__helper-text-message--error[data-v-219a1ffb] {
  color: var(--color-error-text);
}
.textarea__helper-text-message--success[data-v-219a1ffb] {
  color: var(--color-success-text);
}