/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-e1dc2b3e] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.app-navigation-toggle-wrapper[data-v-e1dc2b3e] {
  position: absolute;
  top: var(--app-navigation-padding);
  right: calc(0px - var(--app-navigation-padding));
  margin-right: -44px;
}
button.app-navigation-toggle[data-v-e1dc2b3e] {
  background-color: var(--color-main-background);
}