/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-ebf698d1] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.empty-content[data-v-ebf698d1] {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  /* In case of using in a flex container - flex in advance */
  flex-grow: 1;
}
.modal-wrapper .empty-content[data-v-ebf698d1] {
  margin-top: 5vh;
  margin-bottom: 5vh;
}
.empty-content__icon[data-v-ebf698d1] {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  margin: 0 auto 15px;
  opacity: 0.4;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 64px;
}
.empty-content__icon[data-v-ebf698d1] svg {
  width: 64px !important;
  height: 64px !important;
  max-width: 64px !important;
  max-height: 64px !important;
}
.empty-content__name[data-v-ebf698d1] {
  margin-bottom: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
}
.empty-content__description[data-v-ebf698d1] {
  color: var(--color-text-maxcontrast);
}
.empty-content__action[data-v-ebf698d1] {
  margin-top: 8px;
}
.modal-wrapper .empty-content__action[data-v-ebf698d1] {
  margin-top: 20px;
  display: flex;
}