li.action.active[data-v-24834b9f] {
  background-color: var(--color-background-hover);
  border-radius: 6px;
  padding: 0;
}
.action--disabled[data-v-24834b9f] {
  pointer-events: none;
  opacity: 0.5;
}
.action--disabled[data-v-24834b9f]:hover, .action--disabled[data-v-24834b9f]:focus {
  cursor: default;
  opacity: 0.5;
}
.action--disabled *[data-v-24834b9f] {
  opacity: 1 !important;
}
.action-checkbox[data-v-24834b9f] {
  display: flex;
  align-items: flex-start;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  cursor: pointer;
  white-space: nowrap;
  color: var(--color-main-text);
  border: 0;
  border-radius: 0;
  background-color: transparent;
  box-shadow: none;
  font-weight: normal;
  line-height: 44px;
  /* checkbox/radio fixes */
}
.action-checkbox__checkbox[data-v-24834b9f] {
  position: absolute;
  top: auto;
  left: -10000px;
  overflow: hidden;
  width: 1px;
  height: 1px;
}
.action-checkbox__label[data-v-24834b9f] {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 !important;
  padding-right: 14px !important;
}
.action-checkbox__label[data-v-24834b9f]::before {
  margin: 0 14px 0 !important;
}
.action-checkbox--disabled[data-v-24834b9f],
.action-checkbox--disabled .action-checkbox__label[data-v-24834b9f] {
  cursor: pointer;
}