/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-5b140fb6] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.input-wrapper[data-v-5b140fb6] {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  max-width: 400px;
}
.input-wrapper .action-input__label[data-v-5b140fb6] {
  margin-right: 12px;
}
.input-wrapper[data-v-5b140fb6]:disabled {
  cursor: default;
}
.input-wrapper .hint[data-v-5b140fb6] {
  color: var(--color-text-maxcontrast);
  margin-left: 8px;
}